.App {
  text-align: center;
}

.head{
  width: 100%;
  height: 50px;
  background-color: rgb(245, 90, 90);
  display: flex;
  justify-content: center;
  gap: 10px;
  font-size: small;
}
.head img{
  width: 40px;
  height: 40px;
  align-self: center;
  border-radius: 50%;
}
.cards-list{
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.card{
  display: block;
  height: 168px;
  width: 470px;
  margin: 20px auto;
  overflow: hidden;
  padding: 0px 0px;
  box-shadow: 2px 2px 4px 2px rgba(61, 60, 60, 0.5);
  border-radius: 5px 0px 0px 5px;
} 
.divider{
  display: flex;
  gap: 20px;
}
.image{
   width: 125px;
   height:168px;
   border-radius: 5px;
   align-self: center;
}


.location{
  display: flex;
  gap: 10px;
  padding: 0px 0px;
  margin: 0px 0px;
}
.para,.link{
  font-size: small;
  font-weight: 600; 
  padding: 10px 0px;
  margin: 0px 0px;
  text-align: start;
}

.alpha{
  color: rgb(112, 109, 109);
}
.paras1{
  text-align: start;
  font-size: larger;
  font-weight: bold;
  margin: 0px 0px;
  padding: 0px 0px;
}
.paras2{
  font-weight: 600;
  text-align: start;
  font-size: x-small;
}
.paras3{
  font-weight: 600;
  text-align: start;
  font-size: 10.54px;
  margin: 0px 0px;
  padding: 0px 0px;
}
.map{
 color: rgb(168, 20, 20);
}